@import url(https://fonts.googleapis.com/css?family=Merriweather&display=swap);
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/recipe-finder/static/media/background_new.04c98a9d.png);
  background-repeat: repeat;
  position: relative;
  margin: 0;
  padding: 0;
  min-height: 100%;
  overflow: inherit;
}

html{
  height: 100%;
}

.introBoxText{
  background: rgba(255, 255, 255, 0.4);
  font-size: 12pt;
  padding: 100%;
  position: absolute;
  top: 60%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  font-family: 'Merriweather', serif;
  text-align: center;
  color: #494949;
}

@media (min-width: 900px) {
  .introBoxText{
    font-size: 20pt;
  }
}

img{
  user-drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  cursor: pointer;
}

.buttonTitle{
  font-family: 'Merriweather', serif;
  padding: 5px;
  color: #494949;
  text-align: center;
}

.menuContainer{
  margin-top: 5%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas:
  "introMessage introMessage"
  "mealButton cocktailButton";
}

.introMessage{
  font-family: 'Merriweather', serif;
  background-color: rgb(74,74,74);
  padding: 25px;
  grid-area: introMessage;
  text-align: center;
  margin-bottom: 3%;
  color: white;
}

.mealButton{
  grid-area: mealButton;
  justify-self: right;
  margin-right: 3%;
  margin-bottom: 10%;
}

.cocktailButton{
  grid-area: cocktailButton;
  justify-self: left;
  margin-left: 3%;
  margin-bottom: 10%;
}

@media (min-width: 1200px) {
  .introMessage{
    font-size: 35pt;
  }
}

@media (min-width: 900px) {
  .buttonTitle{
    padding: 10px;
    font-size: 20pt;
  }
}

.row{
  margin-right: 0;
  margin-left: 0;
}

.recipePageItem{
  margin-bottom: 2%;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.itemTitle{
  color: rgb(64, 64, 64);
}

.noResultMsg{
  color: rgb(74, 74, 74);
  text-align: center;
}

.searchBar{
  background: transparent;
  border: 0;
  border-bottom: solid 2px rgb(124,124,124);
  margin-right: 10px;
  font-family: 'Merriweather', serif;
  width: 150px;
  padding: 10px;
  -webkit-transition-duration: 0.4s;
  transition-duration: 0.4s;
  color: rgb(74, 74, 74);
}

.searchBar:focus{
  width: 200px;
}

@media (min-width: 900px) {
  .searchBar:focus{
    width: 550px;
  }

  .searchBar{
    width: 250px;
  }
}

.form{
  margin-top: 5%;
  margin-bottom: 5%;
  text-align: center;
}

.searchButton{
  border: solid 2px rgb(124,124,124);
  background: transparent;
  padding: 10px 30px;
  margin: 0 0.2rem;
  border-radius: 1px;
  font-size: 1rem;
  text-transform: uppercase;
  font-weight: 500;
  margin-left: 0.7rem;
  -webkit-transition-duration: 0.4s;
  transition-duration: 0.4s;
  cursor: pointer;
  color: rgb(104,104,104);
}

.searchButton:hover{
  background: rgb(124,124,124);
  color: white;
}

.recipePageTitle{
  font-family: 'Merriweather', serif;
  font-size: 15pt;
  margin-top: 3%;
  color: rgb(74, 74, 74);
  text-align: center;
}

@media (min-width: 900px) {
  .recipePageTitle{
    font-size: 20pt;
  }
}

#ingredientTable {
  text-align: center;
  font-family: 'Merriweather', serif;
  border-collapse: collapse;
  border: 3px solid #ddd;
  width: 100%;
}

#ingredientTable td, #ingredientTable th {
  border: 1px solid #ddd;
  padding: 8px;
}

#ingredientTable tr:hover {background-color: #ddd;}

#ingredientTable th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: center;
  background-color: #4CAF50;
  color: white;
}

.textTitle{
  font-family: 'Merriweather', serif;
  text-align: center;
}

.recipeInstructionValue{
  line-height: 26pt;
}

.videoFrame{
  position: absolute;
  top: 0;
  left: 0;
  width: 80%;
  height: 80%;
  max-width: 1200px;
  max-height: 750px;
  margin-left: 50%;
  margin-top: 3%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
}

.recipeVideo{
  position: relative;
  padding-bottom: 54.25%;
  padding-top: 25;
  height: 0;
}

.recipeTitle{
  margin-top: 2%;
  text-align: center;
  font-family: 'Merriweather', serif;
}

.recipeContainer{
  margin-top: 2%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-areas:
  "recipePicture"
  "recipeIngredients"
  "recipeInstruction";
}

.recipePicture{
  width: 70%;
  max-width: 700px;
  grid-area: recipePicture;
  justify-self: center;
}

.recipeIngredients{
  grid-area: recipeIngredients;
  justify-self: center;
  margin-top: 5%;
  margin-bottom: 5%;
  text-align: center;
}

.recipeInstruction{
  grid-area: recipeInstruction;
  justify-self: center;
  margin-left: 5%;
  margin-right: 5%;
  margin-top: auto;
  margin-bottom: auto;
  font-family: 'Merriweather', serif;
}

@media (min-width: 1500px) {
  .recipeContainer{
    margin-top: 2%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas:
    "recipePicture recipePicture"
    "recipeIngredients recipeInstruction";
    margin-left: 15%;
    margin-right: 15%;
  }
}

